<template>
  <div class="content">
    <div class="top">
      <div class="logo">
        <img src="./image/logo.png" alt />
      </div>
      <div class="tap">
        <van-tabs v-model="active" color="#4373EA" line-width="50">
          <van-tab title="短信登录">
            <div class="dxdl">
              <div class="input">
                <input class="tel" v-model="tel" type="number" maxlength="11" placeholder="请输入手机号" />
              </div>
              <div class="input">
                <input class="yzm" v-model="yzms" type="number" placeholder="请输入验证码" />
                <p class="yzms" @click="dj" :class="dx==true||tel==''?'':'yzms1'">
                  <span>{{yzm}}{{dx?'s后重新获取':''}}</span>
                  <span></span>
                </p>
              </div>
            </div>
          </van-tab>
          <van-tab title="密码登录">
            <div class="dxdl">
              <div class="input">
                <input class="tel" v-model="tel" type="number" maxlength="11" placeholder="请输入手机号" />
              </div>
              <div class="input pss">
                <input class="yzm" v-model="pass" :type="password" placeholder="请输入密码" />
                <img class="imgs" :src="type==0?img1:img2" alt="" @click="change">
              </div>
              <p class="wjmm" @click="$router.push('/RetrievePass')">忘记密码?</p>
            </div>
          </van-tab>
        </van-tabs>
      </div>
      <div class="login">
        <!-- <button>登录</button> -->
        <van-button :disabled="disabled1" class="btn" type="info"  @click="login">登录</van-button>
      </div>
      <div class="end">
        <p>登录即表示同意 <span @click="$router.push('/agree')">《鑫泉健康网络服务协议》</span></p>
        
      </div>
    </div>
    
  </div>
</template>

<script>
import {request} from '@/api/request'
import url from '@/api/url'
export default {
  name: "index",
  data() {
    return {
      tel:'',
      user:'',
      pass:'',
      yzms:'',
      yzm: "获取验证码",
      disabled: false,
      disabled1:true,
      password:"password",
      active: 0,
      type:0,
      dx: false,
      img2:require('./image/yan.png'),
      img1:require('./image/bi.png')
    };
  },
  watch:{
   
    tel(e){
      if(e!==''&&this.yzms!==''||this.pass!==''&&e!==''){
        this.disabled1=false
      }else{
        this.disabled1=true
      }
      if(e.length>11){
        this.tel=e.slice(0,11)
      }
    },
    pass(e){
       if(e!==''&&this.tel!==''){
        this.disabled1=false
      }else{
        this.disabled1=true
      }
    },
    yzms(e){
       if(e!==''&&this.tel!==''){
        this.disabled1=false
      }else{
        this.disabled1=true
      }
    }
  },
  methods: {
    change(){
      if(this.type==0){
        this.type=1;
        this.password='text'
      }else{
        this.type=0
        this.password='password'
      }
    },
    //登录接口
    login(){
      
      let data
      if(this.active===0){
        data={
          userName:this.tel,
          validCode:this.yzms,
          type:'_sms'
        }
      }else{
         data={
          userName:this.tel,
          passWord:this.pass,
          type:"_normal"
        }
          if ( this.pass == null || this.pass.length < 6||this.pass.length>20) {
        return  this.$toast('密码长度应为6-20');
      }
      var reg = new RegExp(/^(?![^a-zA-Z]+$)(?!\D+$)/);
      if (!reg.test(this.pass)) return this.$toast('密码格式错误');
      }

      this.$http({
        method:'post',
        url:url.login,
        data:data,
        headers:{
          "Content-Type":" application/json"
        }
      })
      .then(res=>{
         if(res.data.code==200){
           this.$toast('登录成功')
         setTimeout(()=>{
             this.$router.push('/home')
         },2000)
         }else{
            this.$toast(res.data.message)
         }
      })
    },
    dj() {
      if (this.tel==='')return false

     
      if (this.yzm === "获取验证码") {
       
        request('get',url.yzm,{phone:this.tel,type:1},res=>{
          if(res.data.meta.code==200){
            this.$toast('验证码发送成功')
             this.dx = true;
        this.yzm = 90;
        let a = setInterval(() => {
          --this.yzm;
          console.log(this.yzm);
          if (this.yzm <= 0) {
            this.yzm = "获取验证码";
            this.dx = false;
            clearInterval(a);
          }
        }, 1000);
          }else{
            this.$toast(res.data.meta.message)
          }
        })
      } else {
        console.log(this.yzm);
        return;
      }
    }
  }
};
</script>

<style scoped lang="less">
.content {
  padding: 1.28rem 1.066667rem;
}
.top {
  text-align: center;

  img {
    width: 2.48rem;
    height: 1.73rem;
  }
}
.tap {
  margin: 0.8rem auto 0;
  .input {
    position: relative;
    width: 100%;
    font-size: 0.426667rem;
    border: .03rem solid #d9d9d9;
    border-radius: 0.64rem;
    margin-bottom: 0.426667rem;
    padding: 0.26667rem 0;

    .tel,
    .yzm {
      width: 90%;
      //  padding: .266667rem .4rem;
      border: 0;
    }
  }
}
.dxdl {
  margin-top: 0.667rem;
}
input::-webkit-input-placeholder {
  color: #d9d9d9;
}
input::-ms-input-placeholder {
  color: #d9d9d9;
}
.yzms {
  position: absolute;
  top: -1px;
  font-size: 0.3733333rem;
  right: 0.5rem;
  color: #7d7e7f;
}
.yzms1 {
  color: #4373ea !important;
}
.login {
  margin-top: 0.746667rem;
  .btn {
    background: linear-gradient(270deg, #4373ea, #439dea);
    color: #fff;
    width: 100%;
    border-width: 0;
    border-radius: 1.06667rem;
    font-size: 0.48rem;
    outline: 0;
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding-left: 0.373333rem;
    padding-right: 0.373333rem;
    box-sizing: border-box;
    font-size: 0.48rem;
    text-align: center;
    text-decoration: none;
    color: #fff;
    line-height: 2.33333333;
    // border-radius: .133333rem;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    box-shadow: 0 0.106667rem 0.16rem 0 rgba(67,115,234,.3);
    overflow: hidden;
    -webkit-appearance: none;
  }
}
.end{
      width: 100%;
    text-align: center;
    margin-top: 0.48rem;
    color: #7D7E7F;
    font-size: .35rem;
  span{
     color: #4373ea
  }
}
.wjmm{
  font-size: .35rem;
     color: #4373ea;
  text-align: right;
}
.pss{
  position: relative;
  img{
    position: absolute;
    display: block;
    width: .48rem;
    height: .32rem;
    margin-top: .08rem;
        right: .5rem;
    top: 15px;
  }
}

</style>



